/* Header styling */

/* About Section Styling */

.about-header {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    height: 90vh;
    margin-left: 10%;
    margin-right: 10%;
    text-align: center;
}

.about-header > img {
    max-width: 100%;
    height: auto;
}

.about-section {
    background-color: #b2baa7;
    color: white;
    display: flex;
    align-items: center;
    padding-left: 10%;
    padding-right: 10%;
    min-height: 500px;
}

.about-section p{
    font-size: 20px;
}

#headshot {
    height: 500px;
}


/* ProjectList styling */


#project-container {
    color: white;
    min-height: 500px;
    padding: 5%;
    text-align:center;
}

#project-container ul{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: space-evenly;
    align-content: center;
}

#project {
    text-align: center;
    width: 30%;
    padding: 5px;
    margin: 10px;
}

#project img{
    width: 80%;
}

#project p {
    padding: 5px;
}

/* Testemonial Section */

#testemonials-container {
    display: flex;
    justify-content: center;
    color: white;
    background: #b2baa7;
    text-align: center;
    padding: 20px;
    min-height: 300px;
}

.testemonial {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 20px;
    width: 40%;
}


